.header {
  border-bottom: 3px solid  $accent;
  &__top {
    background-color: $accent;
    color: $white;
    font-size: em(16); }

  &__info {
    display: flex;
    align-items: center; }

  &__time {
    margin-right: rem(20); }

  &__top-in {
    display: flex;
    justify-content: space-between;
    align-items: center;

    height: $header-top-height;
    padding-top: rem(5);
    padding-bottom: rem(5); }

  &__bottom-in {
    display: flex;
    align-items: center;

    padding-top: rem(15);
    padding-bottom: rem(15); }

  &__nav {
    margin-left: auto; }

  &--no-bdb {
    border-bottom: 0; } }

.lang {
  $this: &;

  &__item {
    display: inline-block;
    &--current {
      text-decoration: underline; } }

  &__link {
    @include hover {
      @extend #{$this}__item--current; } } }
