.contacts-block {
  &__title {
    font-weight: 700;
    font-size: em(24);
    margin-bottom: rem(15); }

  &__contacts {
    margin-bottom: rem(15); } }

.contacts {
  &__item {
    position: relative;
    padding-left: rem(30);

    @include notlast {
      margin-bottom: rem(10); }

    a {
      @include tr(color .3s);
      @include hover {
        color: $accent; } } }

  &__icon {
    position: absolute;
    top: .3rem;
    left: 0;
    fill: $accent;
    font-size: .8em; } }
